
.box {
  border: 4px solid #e0e0e0;
  padding: 16px;
  background: #fbfbfb;
  font-size: 14px;

  h3 {
    border-bottom: 1px solid #e0e0e0;
    padding-bottom: 6px;
    margin-top: -3px
  }
}

.layout-cols-sidebar-left, .layout-cols-sidebar-right {
  .box h4 {
    font-size: 24px;
    border-bottom: 1px solid #e9e9e9;
    padding-bottom: 6px;
    margin-top: -3px;
  }
}

.box + .box, .box-wrapper + .box-wrapper {
  margin-top: 20px
}

.analyze-error {
  text-align: center;

  h3 {
    border: 0;
    padding: 0;
    font-size: 32px
  }
}

.analyze-error-more {
  font-size: 12px
}

.tooltip-info {
  color: #b4991d;
}

.tooltip-info, .box-info {
  border-color: #ffecab;
  background: #fff8df
}

.tooltip-error {
  color: #db2525
}

.tooltip-error, .box-error {
  border-color: #fc7c7c;
  background: #fee
}

.tooltip-help {
  overflow: hidden;
  text-indent: -100%;
  position: absolute;
  top: 10px;
  right: 10px
}

@media(max-width: 765px) {
  .analyze-form-group-test-format .form-row {
    float: none;
    width: auto;
    margin-top: 10px;
    padding-right: 0
  }

  .analyze-form-group-test-format .form-row:first-child {
    margin-top: 0
  }

  .form-row-af-cookies {
    padding: 0
  }
}

#message_item_link[disabled] {
  cursor: not-allowed;

  a[disabled] {
    cursor: not-allowed;
  }
}



